// @flow
import _get from 'lodash/get';
import { OFFER_STEPS } from 'shared/Consts';
import type { Offer } from 'types/Offer';

export const isDisableButton = (offer: Offer, stepId: number): boolean => {
  return _get(offer, 'StepId') !== stepId;
};

const getCounterData = (offer: Offer, pathData: Array<string>): Object => {
  return {
    OfferPrice: _get(offer, pathData.concat('OfferPrice')),
    InspectionContingency: _get(
      offer,
      pathData.concat('InspectionContingency'),
    ),
    SaleOfHomeContingency: _get(
      offer,
      pathData.concat('SaleOfHomeContingency'),
    ),
    ClosingPeriod: _get(offer, pathData.concat('ClosingPeriod')),
    Furniture: _get(offer, pathData.concat('Furniture')),
  };
};

export const mappingCounterData = (offer: Offer): Object => {
  const stepId = _get(offer, ['StepId']);

  switch (stepId) {
    case OFFER_STEPS.counteredBySeller:
      return {
        ...offer,
        MappingCounter: getCounterData(offer, [
          'CounterOffer',
          'SellerCounter',
        ]),
      };
    case OFFER_STEPS.awaitingSellerAction:
    default:
      return {
        ...offer,
        MappingCounter: getCounterData(offer, ['CounterOffer', 'BuyerCounter']),
      };
  }
};
